}

.cardMaster {
  display: flex;
  justify-content: space-between;
  /* width: 900px;  */
}


.filter {
  display: none;
}

.card {
  transform: translate(-1%, -1%);
  width: 400px;
  height: 350px;
  box-shadow: 0 20px 40px -14px #ca9c56;
  margin-left: 18px;
  margin-right: 18px;
  margin-bottom: 36px;

}

.image {
  position: relative;
  display: inline-block;
  text-align: center;
}

.imageTexto {
  position: absolute;
  top: 90%;
  right: 5px;
  color: white;
  font-weight: bolder;
  font-size: large;
  background: #262626;
  border-radius: 10px;
  padding-left: 10px;
  padding-right: 10px;
}

.card .image {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 2%;

}

.card .image img {
  height: 100%;
  transition: .5s;
  background-size: cover;
}

.card:hover .image img {
  opacity: .5;
  transform: translateX(30%);
  /*100%*/

}

.card .details {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transition: .5s;
  transform-origin: left;
  transform: perspective(2000px) rotateY(-90deg);
  border-radius: 2%;
}


.card:hover .details {
  transform: perspective(2000px) rotateY(0deg);
}

.card .details .center {
  padding: 20px;
  text-align: center;
  background: #f7d5a3;
  position: absolute;
  top: 170px;
  transform: translateY(-50%);
  width: 300px;
  height: 200px;
  /* MANEJA EL TAMAÑO DEL DETALLE DE LAS CARD */
}

.card .details .center h1 {
  margin: 0;
  padding: 0;
  line-height: 20px;
  font-size: 20px;
  text-transform: uppercase;
  color: #6d532c;
}

.card .details .center h1 span {
  font-size: 14px;
  color: #262626;
}

.card .details .center p {
  margin: 10px 0;
  padding: 0;
  color: #262626;

}

.card .details .center ul {
  margin: 10px auto 0;
  padding: 0;
  display: table;
}

.card .details .center ul li {
  list-style: none;
  margin: 0 5px;
  float: left;
}



.card .details .center ul li a {
  display: block;
  background: #262626;
  color: #fff;
  width: 60px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  transform: .5s;
  text-decoration: none;
  border-radius: 2px;
}

.ulCenter {
  display: flex;
  /* margin-top: 45px; */
  position: absolute;
  top: 70%;
  right: 115px;

}

.card .details .center ul li a:hover {
  /* background: rgb(248, 162, 3); */
  background: #6d532c;
}

